import React from 'react';
import {render} from 'react-dom';
import {BrowserRouter, Match, Miss} from 'react-router';

import './css/style.css';

import App from './components/App';
import Register from './components/Register';
import Login from './components/Login';
import NotFound from './components/NotFound';
import base from './base';

const MatchWithProps = ({ component:Comp, passProps, ...props}) => (
  <Match {...props} render={(matchedProps) => <Comp {...passProps} {...matchedProps} /> } />
)

const repo = `/${window.location.pathname.split('/')[1]}`; 

export default class Root extends React.Component {
	constructor() {
		super();
		this.state = {
	    loading: true,
	  }
	}
	componentDidMount () {
		this.removeListener = base.onAuth((user) => {
      if (user) {
        this.setState({
        	authed: true,
          loading: false
        })
      } else {
        this.setState({
        	authed: false,
          loading: false
        })
      }
    })
	}
	componentWillUnmount () {
    this.removeListener()
  }

	render(){
		return this.state.loading === true ? <h1 className="loading">Loading</h1> : (
			<BrowserRouter basename={repo}>
				<div>
					<MatchWithProps exactly pattern="/register" component={Register} passProps={{ authed: this.state.authed }} />
					<MatchWithProps exactly pattern="/" component={Login} passProps={{ authed: this.state.authed }} />
					<MatchWithProps exactly pattern="/organization/:storeId" component={App} passProps={{ authed: this.state.authed }} />
					<Miss component={NotFound}/>
				</div>
			</BrowserRouter>	
		)
	}
}

render(<Root/>, document.querySelector('#main'));


