import React from 'react';

import AddFishForm from './AddFishForm';

class Inventory extends React.Component {
	constructor() {
		super();
		this.renderInventory = this.renderInventory.bind(this);
		this.handleChange = this.handleChange.bind(this);
	}
	handleChange(e, key) {
		const fishes = this.props.fishes[key];
		const updatedFish = {
			...fishes,
			[e.target.name]: e.target.value
		}
		this.props.updateFish(key, updatedFish);
	}
	renderInventory(key) {
		const fishes = this.props.fishes[key];
		return(
			<div className="fish-edit" key={key}>
				<input name="name" type="text" value={fishes.name} placeholder="Fish name" onChange={(e) => this.handleChange(e, key)}/>
				<input name="price" type="text" value={fishes.price} placeholder="Fish price" onChange={(e) => this.handleChange(e, key)}/>
				<select name="status" value={fishes.status} onChange={(e) => this.handleChange(e, key)}>
					<option value="available">Fresh</option>
					<option value="unavailable">Sold Out</option>
				</select>
				<textarea name="desc" type="text" value={fishes.desc} placeholder="Fish desc" onChange={(e) => this.handleChange(e, key)}/>
				<input name="image" type="text" value={fishes.image} placeholder="Fish image" onChange={(e) => this.handleChange(e, key)}/>
				<button onClick={() => this.props.removeFish(key)}>Remove Fish</button>
			</div>
		)
	}
	render() {
		return (
			<div>
				<p>Inventory</p>
				{Object.keys(this.props.fishes).map(this.renderInventory)}
				<AddFishForm addFish={this.props.addFish}/>
				<button onClick={this.props.loadSamples}>Load Sample</button>
			</div>
		)
	}
}

Inventory.propTypes = {
	updateFish: React.PropTypes.func.isRequired,
	addFish: React.PropTypes.func.isRequired,
	removeFish: React.PropTypes.func.isRequired,
	loadSamples: React.PropTypes.func.isRequired
}

export default Inventory


