import base from './base'

let userData = {};

export function auth (email, pw, name, phone='') {
  userData = {
    name: name,
    userType: 'users',
    phone: phone
  };
  return base.createUser({
    email: email,
    password: pw
  }, saveUser)
}

export function logout () {
  return base.unauth()
}

export function login (email, pw) {
  return base.authWithPassword({
    email: email,
    password: pw
  })
}

export function resetPassword (email) {
  return base.resetPassword(email)
}

export function saveEvent(name, date, location) {
  return base.post(`events/${name}`, {
    data: {name: name, date: date, location: location},
    then(err){
      
    }
  });
}

export function saveUser (err, user) {
  if(err) return err;
  return base.post(`users/${user.uid}/info`, {
    data: {
      uid: user.uid,
      name: userData.name,
      email: user.email, 
      userType: userData.userType,
      phone: userData.phone,

    },
    then(err){
      return err
    }
  });
}



