import React from 'react'
import { login, resetPassword, logout } from '../auth'
import {getFunName} from '../helpers'

class Login extends React.Component {
  constructor() {
    super();
    this.state = { 
      loginMessage: null,
    }
    this.handleSubmit = this.handleSubmit.bind(this);
    this.resetPassword = this.resetPassword.bind(this);
    this.setErrorMsg = this.setErrorMsg.bind(this);
    this.goToStore = this.goToStore.bind(this);
    this.logOut = this.logOut.bind(this);

    this.renderStore = this.renderStore.bind(this);
    this.renderLogin = this.renderLogin.bind(this);

    this.test = this.test.bind(this);
  }


  goToStore(e) {
    e.preventDefault();
    const storeId = this.storeInput.value;
    this.context.router.transitionTo(`/organization/${storeId}`);
  }
  
  handleSubmit(e){
    e.preventDefault()
    console.log(e.target)
    login(this.email.value, this.pw.value)
      .catch((error) => {
          this.setState(this.setErrorMsg('Invalid username/password.'))
        })
  }
  resetPassword(){
    resetPassword(this.email.value)
      .then(() => this.setState(this.setErrorMsg(`Password reset email sent to ${this.email.value}.`)))
      .catch((error) => this.setState(this.setErrorMsg(`Email address not found.`)))
  }
  setErrorMsg(error) {
    return {
      loginMessage: error
    }
  }

  logOut() {
    logout()
    this.setState({loginMessage: null})
  }

  test(e) {
    e.preventDefault();
    this.context.router.transitionTo(`/register`);
  }
  
  renderStore() {
    return(
      <div>
        <button onClick={this.logOut}>Log Out</button>
        <form action="" className="store-selector" onSubmit={this.goToStore}>
          <h2>Please Input an Event</h2>
          <input type="text" required placeholder="Store Name" defaultValue={getFunName()} ref={(input)=>{this.storeInput = input}}/>
          <button type="submit">Visit Store</button>
        </form>
      </div>
    )   
  }

  renderLogin() {
    return (
      <div className="login">
        <h1> Login </h1>
        <form onSubmit={this.handleSubmit}>
          <div className="form-group">
            <label>Email</label>
            <input className="form-control" ref={(email) => this.email = email} placeholder="Email"/>
          </div>
          <div className="form-group">
            <label>Password</label>
            <input type="password" className="form-control" placeholder="Password" ref={(pw) => this.pw = pw} />
          </div>
          {
            this.state.loginMessage &&
            <div className="alert alert-danger" role="alert">
              <span className="glyphicon glyphicon-exclamation-sign" aria-hidden="true"></span>
              <span className="sr-only">Error:</span>
              &nbsp;{this.state.loginMessage} <a href="#" onClick={this.resetPassword} className="alert-link">Forgot Password?</a>
            </div>
          }
          <button type="submit" name="login" className="btn btn-primary">Login</button>
          <button type="button" name="regeister" className="btn btn-primary" onClick={this.test}>Register</button>
        </form>

      </div>
    )
  }

  render () {
    if(this.props.authed) {
      return <div>{this.renderStore()}</div>
    }
    return <div>{this.renderLogin()}</div>
  }
}

Login.contextTypes = {
  router: React.PropTypes.object
}

export default Login;


