import React, { Component } from 'react'
import {checkPassword, checkType} from '../helpers'
import { auth } from '../auth'

export default class Register extends Component {
  constructor() {
    super();
    this.state = { registerError: null }
    this.handleSubmit = this.handleSubmit.bind(this);
    this.setErrorMsg = this.setErrorMsg.bind(this);
    this.goToHome = this.goToHome.bind(this);
  }

  handleSubmit(e){
    e.preventDefault();
    const correctPassword = checkPassword(this.pw.value, this.pwConfirm.value);
    const name = `${this.firstName.value} ${this.lastName.value}`
    if(!correctPassword) {
      return this.setErrorMsg('Password are not same')
    }
    auth(this.email.value, this.pw.value, name)
      .catch(e => this.setState(this.setErrorMsg(e)))
  }

  setErrorMsg(error){
    const string = checkType(error) === 'string';
    return this.setState({
      registerError: string ? error : error.message
    })
  }
  goToHome() {
    this.context.router.transitionTo(`/`);
  }
  render () {
    return (
      <div className="register">
        <h1>Register</h1>
        <form onSubmit={this.handleSubmit}>
          <div className="form-group">
            <label>First Name</label>
            <input className="form-control" ref={(firstName) => this.firstName = firstName} placeholder="Firstname"/>
          </div>
          <div className="form-group">
            <label>Last Name</label>
            <input className="form-control" ref={(lastName) => this.lastName = lastName} placeholder="Lastname"/>
          </div>
          <div className="form-group">
            <label>Email</label>
            <input className="form-control" ref={(email) => this.email = email} placeholder="Email"/>
          </div>
          <div className="form-group">
            <label>Password</label>
            <input type="password" className="form-control" placeholder="Password" ref={(pw) => this.pw = pw} />
          </div>
          <div className="form-group">
            <label>Confirm Password</label>
            <input type="password" className="form-control" placeholder="Confirm Password" ref={(pwConfirm) => this.pwConfirm = pwConfirm} />
          </div>
          {
            this.state.registerError &&
            <div className="alert alert-danger" role="alert">
              <span className="glyphicon glyphicon-exclamation-sign" aria-hidden="true"></span>
              <span className="sr-only">Error:</span>
              &nbsp;{this.state.registerError}
            </div>
          }
          <button type="submit" className="btn btn-primary">Register</button>
          <button type="button" className="btn btn-primary" onClick={this.goToHome}>Home</button>
        </form>
      </div>
    )
  }
}

Register.contextTypes = {
  router: React.PropTypes.object
}



